<template>
  <div v-if="isShow">
    <div class="dialog fade-enter-done">
      <div class="dialog-bg"></div>
      <div class="animate__animated animate__flip chat__main">
        <div class="figure-pop ">
          <!-- <span class="online_num">在线人数({{ num }})</span> -->
          <!-- chat -->
          <div class="chat_ft">
            <div id="iframe-wrap" class="mobile-width-3">
              <div style="width: '100%'; height: '612px'" class="chat">
                <!-- 展示内容 -->
                <div class="content" ref="content">
                  <div class="message jp-container" ref="room">
                    <div
                      v-for="(v, i) in content"
                      :key="i"
                      :class="[v.isOwn ? 'talk_recordboxme' : 'talk_recordbox']"
                    >
                      <div class="user">
                        <img v-if="v.isOwn" src="./../assets/images/15.jpg" />
                        <img v-else src="./../assets/images/11.jpg" />
                        {{ v.name }}
                      </div>
                      <div class="talk_recordtextbg">&nbsp;</div>
                      <div class="talk_recordtext">
                        <!-- <h3>{{ htmlxss(v.msg) }}</h3> -->
                        <h3>{{ v.msg }}</h3>
                        <span class="talk_time">{{ getDate(v.time) }}</span>
                      </div>
                    </div>
                  </div>
                </div>
                <!-- 发送消息 -->
                <div class="send_msg">
                  <el-input
                    maxlength="15"
                    show-word-limit
                    placeholder="请输入内容"
                    v-model="input"
                    @keyup.enter.native="btn"
                  >
                    <template slot="append">
                      <el-button @click="btn" type="primary" round
                        >发送(enter)</el-button
                      >
                    </template>
                  </el-input>
                </div>
              </div>
            </div>
          </div>
          <!-- close -->
          <div class="close" @click="close">
            <img src="./../assets/images/close.jpg" alt="" />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import store from '../store'
export default {
  props: ['isShow'],
  data() {
    return {
      num: 1,
      input: '',
      content: [],
      id: this.$socket.id,
      sharedState: store.state,
    }
  },
  methods: {
    getDate(t) {
      var time = new Date(t)
      var y = time.getFullYear()
      var m = time.getMonth() + 1
      var d = time.getDate()
      var hh = time.getHours() > 9 ? time.getHours() : '0' + time.getHours() //时
      var mm =
        time.getMinutes() > 9 ? time.getMinutes() : '0' + time.getMinutes() //分
      var ss =
        time.getSeconds() > 9 ? time.getSeconds() : '0' + time.getSeconds() //秒
      var mydate = y + '-' + m + '-' + d + ' ' + hh + ':' + mm
      return mydate
    },
    btn() {
      if (!this.input || !this.sharedState.message) return
      this.$socket.emit('send-message', {
        msg: this.input,
        name: this.sharedState.message,
        istalk: true,
        time: new Date().getTime(),
      })
      //清空消息
      this.input = ''
    },
    scrollBottom() {
      let chat = this.$refs.content
      setTimeout(function() {
        console.log(chat.scrollTop, chat.scrollHeight)
        chat.scrollTop = chat.scrollHeight
      })
    },
    close() {
      this.$emit('closeChat')
    },
  },
  sockets: {
    connect() {
      this.$message({
        message: '连接成功！！！',
        type: 'success',
      })
    },
    exit(data) {
      this.num = data
    },
    disconnect(data) {
      this.$message({
        message: '连接断开！！！',
        type: 'error',
      })
    },
    join_num(data) {
      this.num = data
    },
    send_message(data) {
      let { msg, time, name, id } = data
      this.content.push({
        msg: msg,
        time: time,
        name: name,
        // isOwn: id === this.id,
        isOwn: name === this.sharedState.message,
      })
      this.scrollBottom()
    },
  },
}
</script>

<style scoped lang="scss">
.chat__main {
  height: 100%;
}
.online_num {
  top: 64px;
  left: 345px;
  background: #efefef;
  position: absolute;
}
.jp-container .talk_recordbox {
  min-height: 80px;
  color: #afafaf;
  padding-top: 5px;
  padding-right: 10px;
  padding-left: 10px;
  padding-bottom: 0px;
}

.jp-container .talk_recordbox:first-child {
  border-top: none;
}
.jp-container .talk_recordbox:last-child {
  border-bottom: none;
}
.jp-container .talk_recordbox .talk_recordtextbg {
  float: left;
  width: 10px;
  height: 30px;
  display: block;
  background-image: url(./../assets/images/talk_recordtext.png);
  background-repeat: no-repeat;
  background-position: left top;
}
.jp-container .talk_recordbox .talk_recordtext {
  -moz-border-radius: 5px;
