  font-weight: 100;
}
.jp-container .talk_recordbox .user {
  float: left;
  display: inline;
  height: 45px;
  width: 45px;
  margin-top: 0px;
  margin-right: 5px;
  margin-bottom: 0px;
  margin-left: 0px;
  font-size: 12px;
  line-height: 20px;
  text-align: center;
}
.jp-container .talk_recordboxme {
  display: block;
  min-height: 80px;
  color: #afafaf;
  padding-top: 5px;
  // padding-right: 10px;
  padding-left: 10px;
  padding-bottom: 0px;
}

.jp-container .talk_recordboxme .talk_recordtext {
  -moz-border-radius: 5px;
  -webkit-border-radius: 5px;
  border-radius: 5px;
  background-color: #fcfcfc;
  width: 240px;
  height: auto;
  padding: 5px;
  color: #666;
  font-size: 12px;
  float: right;
}
.jp-container .talk_recordboxme h3 {
  font-size: 14px;
