  z-index: 20;
  text-align: center;
  font-size: 25px;
  background-color: #e40a5d;
  display: inline-block;
  border: none;
  border-radius: 50%;
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.14), 0 4px 8px rgba(0, 0, 0, 0.28);
  cursor: pointer;
  outline: none;
