}
.mobile-width-3 {
  height: 273px !important;
  margin: 0 auto;
  margin-top: 0px;
  padding: 43px 115px 69px 82px;
  width: 605px;
  margin-top: 40px;
  background: url(./../assets/images/bgiphonew.png) no-repeat;
